import React from 'react';
import './App.css';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { CssBaseline } from '@mui/material';
import CookieConsent from 'react-cookie-consent'
import { Bar } from './components/Navigation';
import { Navigate, Route, Routes } from 'react-router-dom';
import { Homepage } from './pages/homepage';
import { PoolMara } from './pages/poolmara/poolmara';
import { Morgana } from './pages/morgana/morgana';
import { Page404 } from './pages/404';
import { ModPage } from './pages/mods';
import { AboutPage } from './pages/about';
import { LessNudityPage } from './pages/lessnudity/lessnudity';
const theme = createTheme({
  palette: {
    mode: 'dark',
    background: {
      default: '#1d1d3d'
    }
  }
})

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline/>
      <Bar/>
      <Routes>
        <Route path = '/' element={<Homepage/>}/>
        <Route path = '/mods' element={<ModPage/>}/>
        <Route path = '/about' element={<AboutPage/>}/>
        <Route path = "/mods/lessnudity" element={<LessNudityPage/>}/>
        <Route path = "/mods/poolmara" element={<PoolMara/>}/>
        <Route path = "/mods/morgana" element={<Morgana/>}/>
        <Route path = "*" element={<Page404/>}/>
      </Routes>
      <CookieConsent>This website uses cookies to enhance the user experience.</CookieConsent>
    </ThemeProvider>
  );
}

export default App;
