import { Button } from "@mui/material";
import { useNavigate } from "react-router-dom";
import { url } from "../utils/defaults";

type  props = {mod: string, setConfirm: any, text: string}

export function NsfwWarning (props: props) {
    const navigate = useNavigate()
    const {mod, text} = props

    const confirm = () => {
        props.setConfirm(true)
        const d = new Date();
        d.setTime(d.getTime() + (10 *24*60*60*1000));
        document.cookie = `nsfw=true; expires=${d.toUTCString()}; path=/`
    }

    const cancel = () => {
        navigate('/')
    }

    const modPage = () => {
        window.location.href = mod
    }

    return (
        <div style={{textAlign: 'center'}}>
            <br/>
            <h1>This page contains NSFW content.</h1>
            {
                text.split('\n').map(a => (
                        <p>{a}</p>
                ))
            }
            <p>If you are under 18 or don't want to see this, please hit the cancel button or visit the mod page.</p>
            <br/>
            <Button style={{ marginLeft: '25px', marginRight: '25px' }} onClick={cancel}>Back to Safety</Button>
            <Button style={{ marginLeft: '25px', marginRight: '25px' }} onClick={modPage}>Mod Page</Button>
            <Button style={{ marginLeft: '25px', marginRight: '25px' }} onClick={confirm}>Continue</Button>
        </div>
    )
}