import { PersonaSubPage } from './personas'
import { KamoshidaSubPage } from './kamoshida'
import React from 'react'
import { Box, Tab, Tabs } from '@mui/material'
import {CardSubPage} from './cards'
import { CharacterSubPage } from './characters'

export function LessNudityPage () {
    const [page, setPage] = React.useState('kamoshida')
    const handleChange = (event: React.SyntheticEvent, newValue: string) => {
        setPage(newValue);
      };
    console.log(page)
    return (
        <div>

            <Box sx={{ width: '100%', backgroundColor: '#252730', position: 'fixed', zIndex: 100}}>
                <Box sx={{ borderBottom: 1, borderColor: '#252730'}}></Box>
                <Tabs variant='scrollable' allowScrollButtonsMobile value={page} onChange={handleChange}>
                    <Tab label="Kamoshida's Palace" value={"kamoshida"}/>
                    <Tab label="Personas" value="persona"/>
                    <Tab label="Characters/Bosses" value="characters"/>
                    <Tab label="Confidant Cards" value="cards"/>
                </Tabs>
            </Box>
            <br/>
            <br/>
            {
                page === 'kamoshida' ? <KamoshidaSubPage/> :
                page === 'persona' ? <PersonaSubPage/> :
                page === 'cards' ? <CardSubPage/> :
                page === 'characters' ? <CharacterSubPage/> :
                <p>WIP</p>

            }
        </div>
    )
}