let string = [""]
export function AboutPage () {
    return (
        <div style={{textAlign: 'center', alignItems: 'center'}}>
            <h4>What, you actually want to know stuff about me?</h4>
            {
                string.map(a => {
                    let str = ""
                    for (let i = 0; i < 100; i++) {                        
                        str += (a)
                    }
                    return <p>{str}</p>
                })
            }
            <p>My name is Bobby and I make mods, or at least I did. You see, I'm currently taking a 2 year break in Idaho for personal reasons.<br/>When I get back though (should be in feb 2025), I'm going to start making mods again.</p>
        </div>
    )
}