import React from "react"
import Compare from 'react-compare-image'
import { NsfwWarning } from "../../components/NsfwWarning"
import { readCookie } from "../../utils/cookiereader"
import { warnings } from "../../utils/defaults"
import chars from './characters.json'



export function CharacterSubPage () {
    let cookie = readCookie(document.cookie).nsfw
    const [con, setCon] = React.useState(Boolean(cookie))
    if (!con) return (
        <NsfwWarning mod="https://gamebanana.com/mods/421893" setConfirm={setCon} text={warnings.characters}/>
    )
    else 
    return (
        <div>
            <h1 style={{fontSize: "50px", textAlign: "center"}}>Characters/Bosses</h1>
            <h1 style={{fontSize: "20px", textAlign: "center"}}>There may be some inconsistancies between these renders and how they actually look.<br/>If in doubt, assume they're better in game.</h1>
            <div style={{width: '60%', marginLeft: 'auto', marginRight: 'auto', backgroundColor: '#141433'}}>
                {
                    chars.map((p, i) => {
                        return (
                            <div style={{width: '95%', marginLeft: "auto", marginRight: 'auto'}}>
                                    <br/>
                                    {
                                        p.caption ? <h2>{p.caption}</h2> : <></>
                                    }
                                    <Compare key={i} rightImageAlt={p.caption} leftImageAlt={p.caption} leftImageLabel='Original' leftImage={p.original} rightImageLabel="Modified" rightImage={p.modified}/>
                                    <br/>
                            </div>
                            
                        )
                    })
                }
            </div>
        </div>
    )
}