import { Button, Divider, Grid, Paper, Typography } from "@mui/material";
import { mods } from '../utils/mods'
import { useNavigate } from 'react-router-dom'
export function Homepage () {
    const Navigate = useNavigate()
    const handleClick = (mod: any) => {
        Navigate(mod.url)
    }
    return (
        <div style={{textAlign: 'center'}}>
            <Typography style={{marginTop: '50px', fontWeight: 'bold', fontSize: '4vw'}}>BobbyTheCatfish's<br/>Persona 5 Mods</Typography>
            <br/>
            <br/>
            <Divider style={{width: '70%', marginLeft: 'auto', marginRight: 'auto'}}/>
            <Typography style={{marginTop: '50px', fontWeight: 'bold'}} variant="h5">Most Recent</Typography>
            <br/>
            <br/>
            <Grid container>
                {
                    mods.map(m => (
                        <Paper onClick={() => handleClick(m)} style={{width: '25vw', marginLeft: 'auto', marginRight: 'auto', cursor: 'pointer'}} elevation={5}>
                            <Typography marginTop="5%" fontSize="3vw" overflow="hidden" style={{userSelect: 'none'}}>{m.name}</Typography>
                            <img src={m.image} alt={m.name} width="90%" style={{marginTop: '5%', marginBottom:'5px'}}/>
                        </Paper>
                    ))
                }
            </Grid>
            <br/>
            <br/>
            <br/>
            <Button variant="outlined" onClick={() => Navigate('/mods')}>See All Mods</Button>
        </div>
    )
}