import { Grid } from '@mui/material'
import React from 'react'
import Compare from 'react-compare-image'
import { NsfwWarning } from '../../components/NsfwWarning'
import { readCookie } from '../../utils/cookiereader'
import { warnings } from '../../utils/defaults'
import cards from './cards.json'
export function CardSubPage () {
    let cookie = readCookie(document.cookie).nsfw
    const [con, setCon] = React.useState(Boolean(cookie))
    console.log(cookie)
    console.log(con)
    if (!con) return (
        <NsfwWarning mod="https://gamebanana.com/mods/421893" setConfirm={setCon} text={warnings.cards}/>
    )
    else 
    return (
        <div>

        <h1 style={{fontSize: "50px", textAlign: "center"}}>Confidant Cards</h1>
        <h1 style={{fontSize: "20px", textAlign: "center"}}>Modified is on the right</h1>
        <Grid container style={{width: '80%', marginLeft: 'auto', marginRight: 'auto', backgroundColor: '#141433'}}>
            {
                cards.map((c, i) => {
                    return (
                        <Grid item xs={3.5} style={{marginLeft: "auto", marginRight: 'auto', textAlign: 'center'}}>
                                <br/>
                                {
                                    c.caption ? <h2 style={{fontSize: 'min(40px, 5vw)'}}>{c.caption}</h2> : <></>
                                }
                                <Compare handleSize={20}  key={i} leftImageAlt={c.caption} rightImageAlt={c.caption} leftImage={c.original} rightImage={c.modified}/>
                                <br/>
                        </Grid>
                        
                        )
                    })
                }
        </Grid>
    </div>
    )
}