import { Button } from '@mui/material'
import React from 'react'
import Compare from 'react-compare-image'
import { NsfwWarning } from '../../components/NsfwWarning'
import pictures from './comparison.json'
import { warnings } from '../../utils/defaults'
import { readCookie } from '../../utils/cookiereader'


export function KamoshidaSubPage () {
    let cookie = readCookie(document.cookie).nsfw
    const [con, setCon] = React.useState(Boolean(cookie))
    if (!con) return (
        <NsfwWarning mod="https://gamebanana.com/mods/421893" setConfirm={setCon} text={warnings.kamoshida}/>
    )
    else 
    return (
        <>
            <h1 style={{fontSize: "50px", textAlign: "center"}}>Kamoshida's Palace</h1>
            <div style={{width: '60%', marginLeft: 'auto', marginRight: 'auto', backgroundColor: '#141433'}}>
                {
                    pictures.map((p, i) => {
                        return (
                            <div style={{width: '95%', marginLeft: "auto", marginRight: 'auto'}}>
                                    <br/>
                                    {
                                        p.comment ? <h2>{p.comment}</h2> : <></>
                                    }
                                    {
                                        p.link ? <Button onClick={() => window.open(p.link)}>See What Happened</Button> : <></>
                                    }
                                    <Compare key={i} leftImageAlt={p.alt} rightImageAlt={p.alt} leftImageLabel='Original' leftImage={p.original} rightImageLabel="Modified" rightImage={p.modified} sliderPositionPercentage={(p.percent ?? 50) / 100}/>
                                    <br/>
                            </div>
                            
                        )
                    })
                }
            
            </div>
            </>
    )
}